<template>
  <el-form ref="form" :model="user" :rules="rules" label-width="80px">
    <el-form-item label="" prop="oldPassword">
      <el-input v-model="user.oldPassword" placeholder="" type="password" show-password/>
    </el-form-item>
    <el-form-item label="" prop="newPassword">
      <el-input v-model="user.newPassword" placeholder="" type="password" show-password/>
    </el-form-item>
    <el-form-item label="" prop="confirmPassword">
      <el-input v-model="user.confirmPassword" placeholder="" type="password" show-password/>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" size="mini" @click="submit"></el-button>
      <el-button type="danger" size="mini" @click="close"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import { updateUserPwd } from "@/api/system/user";

export default {
  data() {
    const equalToPassword = (rule, value, callback) => {
      if (this.user.newPassword !== value) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      user: {
        oldPassword: undefined,
        newPassword: undefined,
        confirmPassword: undefined
      },
      // 
      rules: {
        oldPassword: [
          { required: true, message: "", trigger: "blur" }
        ],
        newPassword: [
          { required: true, message: "", trigger: "blur" },
          { min: 6, max: 20, message: " 6  20 ", trigger: "blur" }
        ],
        confirmPassword: [
          { required: true, message: "", trigger: "blur" },
          { required: true, validator: equalToPassword, trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    submit() {
      this.$refs["form"].validate(valid => {
        if (valid) {
          updateUserPwd(this.user.oldPassword, this.user.newPassword).then(response => {
            this.$modal.msgSuccess("");
          });
        }
      });
    },
    close() {
      this.$tab.closePage();
    }
  }
};
</script>
